import React, { useState, useEffect } from 'react';
import firebase from 'firebase';
import {Plugins} from '@capacitor/core';
import DataProps from '../components/DataProps';
import {useDocument} from "react-firebase-hooks/firestore";
import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, 
    IonInput, IonItem, IonLabel, IonList, IonButton, IonDatetime } from '@ionic/react';
import { Controller, useForm } from 'react-hook-form';
import './DataInput.css';


//interface TextEntryProps {
//    onSubmit: (homeName: string, homeHours: number, homeDate: string) => void;
//}
export function debugInfo(logInfo: DataProps){
    console.log(logInfo.name,logInfo.hours,logInfo.date, logInfo.location);
};

export function clearInfo(info: DataProps){
    info.name='';
    info.hours=0;
    info.date='';
    info.location='';
};
var today = new Date();

const todaysDate = () => {
    return today.toDateString(); 
};



const DataInput: React.FC<DataProps> = (props: DataProps) => {
    const [item, setItem] = useState<DataProps>({
        name: '',
        hours: 0,
        date: '',
        location: ''
      });
    
    const [value, loading, error] = useDocument(
        firebase.firestore().doc("items/" + props.initialValue),
        {
            snapshotListenOptions: {includeMetadataChanges: true}
        }
    );

      
    useEffect(() => {
        !loading && props.initialValue && value?.exists && setItem(value.data()?.name);
    }, 
    [loading, props.initialValue, value]);
    
    const addLocation = async () => {
        const {Geolocation} = Plugins;
        const position = await Geolocation.getCurrentPosition();
        const newPos =  "Latitude: " + position.coords.latitude + " Longitude: " + position.coords.longitude; 
        return setItem({
            ...item,
            location: newPos
          });
    }

    const getValid = async () => {
        console.log("test!");
        //props.onSubmit(name!, hours!, date!)

        let collectionRef = firebase.firestore().collection("data");
        if(props.name) {
            await(collectionRef).doc(props.name).set({title: item.name, content: item.hours, 
                createdOn: new Date().getTime(), location:item.location ? item.location: "",}, {merge:true});
            clearInfo(item);
            setItem(item);
                props.clear();
        }
        else {
            await collectionRef.add({title: item.name, content: item.hours, 
                createdOn: new Date().getTime(), location:item.location ? item.location: "",
                });
            clearInfo(item);
            setItem(item);
            props.clear();
        }

    };
    
      const updateField = (e: any) => {
        e.preventDefault();
        console.log(item.name,item.hours);
        debugInfo(item);
        setItem({
          ...item,
          [e.target.name]: e.target.value
        });
      };
    
    return (
        <IonPage>
          <IonHeader>
            <IonToolbar>
              <IonTitle>Enter Data</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>

            <form onSubmit={getValid}>
                <IonList>
                    <IonItem>
                        <IonLabel position="stacked">Name:</IonLabel>
                        <IonInput required={true} value={item.name} onIonChange={updateField}></IonInput>
                    </IonItem>
                    <IonItem>
                        <IonLabel position="stacked">Hours Worked:</IonLabel>
                        <IonInput required={true} type="number" value={item.hours} onIonChange={updateField}></IonInput>
                    </IonItem>
                    <IonItem>
                        <IonLabel position="stacked">Date:</IonLabel>
                        <IonDatetime placeholder={todaysDate()} value={item.date} onIonChange={updateField}></IonDatetime>
                    </IonItem>
                </IonList>
                <IonButton type="submit">Submit</IonButton>
            </form>
          </IonContent>
        </IonPage>
      );
};
export default DataInput;
